'use client';

import { useEffect, useState } from 'react';
import Link from 'next/link';

interface Clinic {
  _id: string;
  name: string;
  address: {
    street: string;
    city: string;
    governorate: string;
  };
  phone: string;
  operating_hours: string;
}

export default function ClinicsPage() {
  const [clinics, setClinics] = useState<Clinic[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchClinics();
  }, []);

  const fetchClinics = async () => {
    try {
      const response = await fetch('/api/clinics');
      const data = await response.json();
      setClinics(data);
    } catch (error) {
      console.error('Error fetching clinics:', error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) return <div className="text-center py-12">Loading clinics...</div>;

  return (
    <div className="max-w-7xl mx-auto px-4 py-12">
      <h1 className="text-4xl font-bold mb-8">Our Clinics</h1>

      {clinics.length === 0 ? (
        <div className="bg-white rounded-lg shadow p-6 text-center">
          <p className="text-gray-600">No clinics found.</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {clinics.map((clinic) => (
            <Link key={clinic._id} href={`/clinics/${clinic._id}`}>
              <div className="bg-white rounded-lg shadow p-6 hover:shadow-lg cursor-pointer">
                <h2 className="text-2xl font-bold mb-2">{clinic.name}</h2>
                <p className="text-gray-600 mb-2">
                  üìç {clinic.address.street}, {clinic.address.city}
                </p>
                <p className="text-gray-600 mb-2">
                  üèõÔ∏è {clinic.address.governorate}
                </p>
                <p className="text-gray-600 mb-2">
                  üìû {clinic.phone}
                </p>
                <p className="text-gray-600">
                  ‚è∞ {clinic.operating_hours}
                </p>
              </div>
            </Link>
          ))}
        </div>
      )}
    </div>
  );
}
